.Sidebar {
    z-index: 999;
    box-sizing: border-box;
    position : fixed;
    border-right: #eaecef 1px solid;
    top: 3rem;
    left: -300px;
    width: 300px;
    height: 100%;
    background: rgba(255,255, 255, 0.9);
   
    
    transition: all 300ms linear;
}
.Sidebar-wrapper {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 2rem 2rem;

}
.img-wrapper {
    width: 100%;
    height: 235px;
}
.img-wrapper img{
    border-radius: 50%;
   
    width: 100%;
   
}
.title-text {
    font-size: 1.2rem;
    padding-top: 1.2rem;
    text-align: center;
}
.text {
    font-size: 1rem;
    padding-top: 1rem;
}
.text a{
    font-size: 1rem;
    padding-top: 1rem;
}

.Sidebar a{
    padding-top: 0;
    font-size: .8rem;
}
.Shadow{
    z-index:998;
    position: fixed;
    top:0;
    height:100%;
    box-shadow: 0 0 0 9999px rgba(0,0,0,0.5) ;
    transition: all 300ms linear;
}
