.masonry-wrapper {
    padding: 4.0rem 1.5rem;
    max-width: 960px;
    margin-right: auto;
    margin-left: auto;
  }
.masonry-item {
    display: inline-block;
    vertical-align: top;
    margin-bottom: 10px;
  }
.masonry-item img {
    width: 100%;
    height: auto;
    border-radius: 4px;
    overflow: hidden;
    filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, .3));
}
