.blog-content {
	text-align: left;
	display: inline-block;
	box-sizing: border-box;
	width: 100%;
	max-width: 960px;
	height: auto;
	margin: 0;
	margin-top: 2.5rem;
	border: 1px #eaecef solid;
	border-radius: 10px;
	white-space: pre-wrap;
	text-align: center;
}


.content-title{
	padding:1rem 1rem 0.5rem 1rem;
	border-bottom: #eaecef 1px solid;
	font-size : 1.3rem;
	font-weight: bold;
}
.content-text {
	padding : 0.5rem 1rem 1rem 1rem;
	word-break:break-all;
}

h1, p{
	padding:0;
	margin:0;
}


