.page.wrapper{
    box-sizing: border-box;
    width:100%;
    height:100%;
    padding: 4.0rem 4.0rem;
    text-align: center;
}

.masonry {
    columns: 2;   
    column-gap: 10px;
}
@media only screen and (max-width: 1023px) and (min-width: 768px) { 
  .masonry {
    columns: 3;
  }
}
@media only screen and (min-width: 1024px) {
  .masonry {
    columns: 4;
  }
}

