.Header {
    z-index: 1000;
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: 3rem;
    box-sizing: border-box;
    border-bottom: #eaecef;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    padding: 0.8rem 2.5rem;
    background: rgba(255,255, 255, 0.9);
}
.toggle {
    box-sizing: border-box;
    position: fixed;
    line-height: 1.3rem;
    top: 0.8em;
    left: 0.8rem;
   
    
    cursor: pointer;
}

.title {
    display: inline-block;
    height: 1.4rem;
    line-height: 1.4rem;
}
.router.wrapper{
    display: inline-block;
    float: right;
    height: 1.4rem;
    line-height: 1.4rem;
}
.router.content{
    display: inline-block;
    margin-left: .9rem; 
    height: 1.4rem;
    line-height: 1.4rem;
}
.router.content:hover{
    border-bottom:slateblue;
    border-bottom-style:solid;
    border-bottom-width:3px;
}
a {
    color: black;
    text-decoration: none;
}
a:hover{
    color: inherit;
}
